#workbench {
  position: relative;
    background-image: url("../images/Screen Shot 2022-04-24 at 7.05.52 PM.png");
    /* background-position: top center; */
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    
    /* top: 0; */
  }

  #welcome button {
    border: 3px solid olive;
    border-radius: 10px;
    width: 100px;
    height: 54px;
    background-color: rgba(14, 94, 14, 0.435);
    color: white;
    font-family: fantasy;
    letter-spacing: .95px;
    font-size: 17px;
    transition: all 33ms ease-in-out;
    cursor: pointer;
  }
  
  #workbench > button{
    margin-left: 10px;
    margin-top: 10px;
    border: 1px solid black;
    border-radius: 3px;
    box-shadow: 0px 8px 16px 0px rgba(238, 33, 33, 0.704);
    border: 3px solid olive;
    border-radius: 100px 100px 100px 100px;
    width: 100px;
    height: 54px;
    background-color: rgba(14, 94, 14, 0.435);
    color: white;
    font-family: fantasy;
    letter-spacing: .95px;
    font-size: 17px;
    transition: all 33ms ease-in-out;
    cursor: pointer;
  }

  .instructions {
    width: 18%;
    font-size: 27px;
    position: absolute;
    right: 0%;
    color: white;
    background-image: '';
  }
  
  .bench-holo {
    position: absolute;
    right: 16.5%;
    bottom: 0%;
    width: 20%;

  }
  
  #workbench ul{
    font-size: 10;
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }

  
