  /*sidebar buttons */
  button.sidebar-buttons{
    border-radius: 0px 10px 10px 0px;
    border: 3px solid olive;
    width: 100px;
    height: 54px;
    background-color: rgba(14, 94, 14, 0.435);
    box-shadow: 0px 8px 16px 0px rgba(238, 33, 33, 0.704);
    color: white;
    font-family: fantasy;
    letter-spacing: .95px;
    font-size: 17px;
    transition: all 33ms ease-in-out;
    cursor: pointer;

  }

  button.sidebar-buttons:hover {
    transform: translateX(5px);
  }

.dropdown-crystal{
    width: 70px;
    position: relative;
    display: inline-block;
}


.dropdown-content-crystal {
  display: none;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  position: absolute;
  left: 140%;
  top: 0%;
  font-size: 20;
  background-color: #fafeffa2;
  border-radius: 4px;
  min-width: 70px;
  box-shadow: 0px 8px 16px 0px rgb(0 0 0 / 20%);
  z-index: 1;
  cursor: pointer;
  color: #242424;
  font-family: arial;
  padding: 12px;
}

.dropdown-content-crystal > p:hover {
  color: olive;
    
}


.dropdown-crystal:hover .dropdown-content-crystal{
    display: flex;
}

