#options-container {
  position: absolute;
  background-image: url("../images/computer-terminal.jpg");
  height: 500px;
  width: 600px;
  top: 35%;
  background-position-x: center;
  background-position-y: center;
  opacity: .85;
  margin-left: 150px;
  margin-right: 50px;
  box-shadow: 0px 8px 16px 0px rgba(0,0.9,0.8,0.8);
}

#options-container  button{
  position: absolute;
  bottom: 5%;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center; 
  width: 52px;
  border: 3px solid olive;
  border-radius: 10px;
  width: 100px;
  height: 54px;
  background-color: rgba(14, 94, 14, 0.435);
  box-shadow: 0px 8px 16px 0px rgba(238, 33, 33, 0.704);
  color: white;
  font-family: fantasy;
  letter-spacing: .95px;
  font-size: 17px;
  transition: all 33ms ease-in-out;
  cursor: pointer;
}

#options-container > h3:first-of-type {
  font-size: 30px;
  text-align: center;
  color: rgb(32, 32, 32);
  padding-top: 30px;
  padding-bottom: 30px;
  border-bottom: 1px solid gray;
}

#options-container > p:first-of-type {
  font-size: 20px;
  color: rgb(54, 54, 54);
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 20px;
  text-align: center;
  line-height: 1.6;


}

#options-container > img:first-of-type {
  width: 28px;
  margin-left: 283px;
  padding-top: 40px;
}

.workbench-sidebar {
  width: 314px;
}

/* Lightsaber styling */

#blade-Green {
  display: none;
  position: absolute;
  left: 72.7%;
  height: 474px;
  width: 14px;
  bottom: 37%;
  background-color: rgb(210, 208, 234);
  border-radius: 26px 26px 0px 0px;
  animation: flicker-green 3s linear infinite;
}

@keyframes flicker-green {
  0%{box-shadow: 0 -20px 80px 40px rgba(10, 200, 0, 0.8);}
  12.5%{box-shadow: 0 -20px 80px 45px rgba(10, 200, 0, 0.8);}
  25%{box-shadow: 0 -20px 80px 50px rgba(10, 200, 0, 0.8);}
  37.5%{box-shadow: 0 -20px 80px 55px rgba(10, 200, 0, 0.8);}
  50%{box-shadow: 0 -20px 80px 60px rgba(10, 200, 0, 0.8);}
  62.5%{box-shadow: 0 -20px 80px 55px rgba(10, 200, 0, 0.8);}
  75%{box-shadow: 0 -20px 80px 50px rgba(10, 200, 0, 0.8);}
  87.5%{box-shadow: 0 -20px 80px 45px rgba(10, 200, 0, 0.8);}
  100%{box-shadow: 0 -20px 80px 40px rgba(10, 200, 0, 0.8);}
}

